body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #ffe6e6;
  text-align: center;
}

.container {
  max-width: 800px;
  margin: auto;
  padding: 20px;
}

.top-images {
  display: flex;
  justify-content: center;
  gap: 20px; /* Space between pictures */
  margin-bottom: 20px;
}

.top-images img {
  width: 150px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Add shadow effect */
}

h2 {
  margin: 10px 0;
  color: #ff0066;
}

p {
  margin: 5px 0;
  color: #ff3399;
}

h1 {
  margin: 20px 0;
  color: #ff0033;
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

button {
  padding: 10px 20px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

button:hover {
  transform: scale(1.1);
}

.yes-button {
  background-color: #4caf50;
  color: white;
}

.no-button {
  background-color: #f44336;
  color: white;
  position: absolute;
}

.gifs-container {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.gifs-container img {
  width: 150px;
  height: auto;
  border-radius: 10px;
}
